body {
  width: 100%;
  background-color: rgb(0, 247, 255);

}
.navbar {
  width: 100%;
  height: 80px;
  color: rgb(0, 0, 0);
  background-color: rgb(0, 247, 255);
  display: flex;
  justify-content: center;
  align-items: center;
}
span {
    color: rgb(0, 0, 0);
}
.anggrek {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  background-color: rgb(255, 255, 255);
  color: purple;
  cursor: pointer;
}
.home, .aboutme, .account {
  background-color: rgba(153, 0, 255, 0.897);
  width: 30%;
  height: 70px;
  margin-bottom: 10px;
  margin-top: 10px;
  border-radius: 0%;
  display: flex;
  justify-content: center;
  align-items: center;
}
img {
  border: 5px solid rgb(0, 0, 0);
  border-radius: 10%;
  width: 250px;
  height: 300px;
  margin-left: 5px;
  margin-top: 9px;
}
.tampilanhome {
  width: 10000px;
  height: 330px;
  background-color: rgb(89, 215, 253);
  display: flex;
 
  margin-top: 40px;
  box-shadow: 0 3px 3px 0 rgb(0, 162, 255);
}
h1 {
  margin-left: 20%;
}
